@import "src/common/styles/sass/Variables";
@import "src/common/styles/sass/Mixins";

.skill {
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  word-break: break-word;
  padding: 20px;
  box-sizing: border-box;
  margin: 6px;

  &:hover {
    background-color: $primaryColor;
    border-radius: 10px;
    height: 100%;
  }

  .icon {
    height: 60px;
    width: 60px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
  }

  .title {
    text-align: center;
    width: 100%;
    @include text($titleFontFamily, 18px, 1.2, 700);
    letter-spacing: 2px;
    margin: 10px 0 5px;
  }

  .description {
    text-align: start;
    width: 100%;

    @include text($textFontFamily, 14px, 1.8, 700);
    font-weight: 400;
    word-break: break-word;
  }
}



