@import "src/common/styles/sass/Variables";
@import "src/common/styles/sass/Mixins";

.mainBlock {
  height: calc(100vh);
  background-color: $bgColor;
  position: relative;

  .mainContainer {
    height: calc(100vh);

    .text {
      width: 300px;
      padding-top: 20px;
      @include text($titleFontFamily, 20px, 1.2, 700);
      color: $textColor;
      z-index: 0.5;
      margin: 30px 0;

    }

    .photoContainer {
      position: relative;
      width: 300px;
      height: 400px;

      .photo {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        position: absolute;
        z-index: 1;
      }

      &:after {
        content: '';
        position: absolute;
        width: 300px;
        height: 400px;
        top: -20px;
        right: -20px;
        z-index: 0;
        border: 5px solid $primaryColor;
      }
    }
  }
}

.icons {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100px;
  padding-top: 30px;
}






